<template>
  <div class="hostservice-netfilter-manRule" style="width: 100%;padding: 8px;">
    <g-el-table
      :tableHeadShow="tableHeadShow"
      :url="url"
      :type="type"
      :cloumns="cloumns"
      :params="params"
      :pageParams="pageParams"
      @selectChange="selectChange"
      ref="hostservice_netfilter_manRule"
    >
      <template v-slot:table_header>
        <el-button type="danger" size="mini" @click="delAll(selectData)"></el-button>
        <el-button type="primary" size="mini" @click="refresh"></el-button>
        <el-button type="primary" size="mini" @click="rule_on(selectData)"></el-button>
        <el-button type="primary" size="mini" @click="rule_off(selectData)"></el-button>
        <el-button type="primary" size="mini" @click="addRule()"></el-button>
        <el-button type="primary" size="mini" @click="lookRule()"></el-button>
      </template>

      <!--  -->
      <template v-slot:alias="props">
        <el-input
          v-model="params.alias"
          size="mini"
          placeholder=""
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>

      <!--  -->
      <template slot="status" slot-scope="props">
        <div v-if="props.data.row.status == 1">
          <span v-html="icon.stateIcon.success"></span>
        </div>
        <div v-else-if="props.data.row.status == 0">
          <span v-html="icon.stateIcon.error"></span>
        </div>
      </template>
      <template v-slot:status_head="props">
        <el-select v-model="params.status" clearable @change="search" size="mini" placeholder="">
          <el-option
            v-for="item in select.status"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>

      <!-- IP -->
      <template v-slot:src_ip="props">
        <el-input
          v-model="params.src_ip"
          size="mini"
          placeholder=""
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>

      <!--  -->
      <template v-slot:src_port="props">
        <el-input
          v-model="params.src_port"
          size="mini"
          placeholder=""
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>

      <!--  -->
      <template slot="direction" slot-scope="props">
        <div v-if="props.data.row.direction == 2">
          <span v-html="icon.directionIcon.inbound"></span>
        </div>
        <div v-else-if="props.data.row.direction == 1">
          <span v-html="icon.directionIcon.outbound"></span>
        </div>
      </template>
      <template v-slot:direction_head="props">
        <el-select v-model="params.direction" clearable @change="search" size="mini" placeholder="">
          <el-option
            v-for="item in select.direction"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>

      <!-- IP -->
      <template v-slot:dest_ip="props">
        <el-input
          v-model="params.dest_ip"
          size="mini"
          placeholder=""
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>

      <!--  -->
      <template v-slot:dest_port="props">
        <el-input
          v-model="params.dest_port"
          size="mini"
          placeholder=""
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>

      <!--  -->
      <template slot="protocols" slot-scope="props">
        <div v-if="props.data.row.protocols == 1">TCP</div>
        <div v-else-if="props.data.row.protocols == 2">UDP</div>
        <div v-else-if="props.data.row.protocols == 3">All</div>
      </template>
      <template v-slot:protocols_head="props">
        <el-select v-model="params.protocols" clearable @change="search" size="mini" placeholder="">
          <el-option
            v-for="item in select.protocols"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>

      <!--  -->
      <template slot="process" slot-scope="props">
        <div v-if="props.data.row.process == 1">
          <span v-html="icon.stateIcon.success"></span>
        </div>
        <div v-else-if="props.data.row.process == 0">
          <span v-html="icon.stateIcon.error"></span>
        </div>
      </template>
      <template v-slot:process_head="props">
        <el-select v-model="params.process" clearable @change="search" size="mini" placeholder="">
          <el-option
            v-for="item in select.process"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>

      <!--  -->
      <template v-slot:operation1="props">
        <el-button type="primary" size="mini" @click="manRule_edit(props.data.row)"></el-button>
        <el-button type="danger" size="mini" @click="manRule_del(props.data.row)"></el-button>
      </template>
    </g-el-table>
  </div>
</template>
<script>
import addRule from "views/hostservice/netfilter/manRule/addRule/"; //
import lookRule from "views/hostservice/netfilter/manRule/lookRule/"; //
import manRule_edit from "views/hostservice/netfilter/manRule/manRule_edit/"; //

export default {
  name: "hostservice_netfilter_manRule",
  data() {
    return {
      url: "/host/" + this.host_id + "/netfilter/handle/rule/list",
      type: "post",
      selectData: [],
      cloumns: [
        {
          type: "selection",
          fixed: "left"
        },
        {
          type: "index",
          // fixed: "left",
          label: "",
          width: "60px",
          second_head: {
            type: "index",
            width: "60px",
            label: " ",
            indexMethod: function(index) {
              return index + 1;
            }
          }
        },
        {
          label: "",
          sortable: true,
          prop: "alias",
          second_head: {
            prop: "alias",
            templateHeadName: "alias",
            width: "150px"
          }
        },
        {
          label: "",
          sortable: true,
          prop: "status",
          second_head: {
            isTemplate: true,
            templateName: "status",
            templateHeadName: "status_head",
            width: "150px",
            prop: "status"
          }
        },
        {
          label: "IP",
          prop: "src_ip",
          sortMethod: this.$tools.src_ipSort,
          sortable: true,
          second_head: {
            templateHeadName: "src_ip",
            width: "150px",
            prop: "src_ip"
          }
        },
        {
          label: "",
          prop: "src_port",
          sortable: true,
          second_head: {
            templateHeadName: "src_port",
            prop: "src_port",
            width: "150px"
          }
        },
        {
          label: "",
          prop: "direction",
          sortable: true,
          second_head: {
            isTemplate: true,
            templateName: "direction",
            templateHeadName: "direction_head",
            prop: "direction",
            width: "150px"
          }
        },
        {
          label: "IP",
          prop: "dest_ip",
          sortMethod: this.$tools.dest_ipSort,
          sortable: true,
          second_head: {
            templateHeadName: "dest_ip",
            prop: "dest_ip",
            width: "150px"
          }
        },
        {
          label: "",
          prop: "dest_port",
          sortable: true,
          second_head: {
            templateHeadName: "dest_port",
            prop: "dest_port",
            width: "150px"
          }
        },
        {
          label: "",
          prop: "protocols",
          sortable: true,
          second_head: {
            isTemplate: true,
            templateName: "protocols",
            templateHeadName: "protocols_head",
            prop: "protocols",
            width: "150px"
          }
        },
        {
          label: "",
          prop: "process",
          sortable: true,
          second_head: {
            isTemplate: true,
            templateName: "process",
            templateHeadName: "process_head",
            prop: "process",
            width: "150px"
          }
        },
        {
          label: "",
          prop: "operation1",
          isTemplate: true,
          templateName: "operation1",
          width: "160px",
          second_head: {
            isTemplate: true,
            templateName: "operation1",
            width: "160px",
          }
        }
      ],
      icon: {
        stateIcon:this.$icons.stateIcon,
        linkIcon:this.$icons.linkIcon,
        directionIcon:this.$icons.directionIcon
      },
      tableHeadShow: true,
      params: {
        page: 1,
        limit: 10,
        alias: "", //
        status: "", // 
        src_ip: "", // ip
        src_port: "", // 
        direction: "", // 
        dest_ip: "", // ip
        dest_port: "", // 
        protocols: "", // 
        have_mac_addr: "", // 
        process: "" // 
      },
      select: {
        // 
        status: [
          {
            id: 1,
            name: ""
          },
          {
            id: 0,
            name: ""
          }
        ],
        // 
        self_learn: [
          {
            id: 1,
            name: ""
          },
          {
            id: 2,
            name: ""
          }
        ],
        // 
        direction: [
          {
            id: 2,
            name: ""
          },
          {
            id: 1,
            name: ""
          }
        ],
        // 
        protocols: [
          {
            id: 1,
            name: "TCP"
          },
          {
            id: 2,
            name: "UDP"
          },
          {
            id: 3,
            name: "All"
          }
        ],
        // 
        process: [
          {
            id: 0,
            name: ""
          },
          {
            id: 1,
            name: ""
          },
        ]
      },
      pageParams: {
        layout: "total, sizes, prev, pager, next, jumper",
        pageAlign: "left",
        pageSizes: [10, 20, 30]
      }
    };
  },
  props: {
    item: Object,
    host_id: Number,
    layerid: {
      type: String
    }
  },
  computed: {},
  mounted:function(){
  },
  methods: {
    selectChange(select) {
      this.selectData = select;
    },
    search() {
      this.params.page = 1
      this.reload()
    },
    // 
    refresh() {
      this.reload();
    },
    // 
    delAll(item) {
      let _this = this;
      let fids =""
      if (Array.isArray(item)) {
        if (this.$tools.isArrayNull(item)) {
          this.$message({
            type: "error",
            message: "!"
          });
          return;
        }
        fids = this.$tools.mergeParam(item,'fid')
      } else {
        fids = item.fid
      }
      this.$isShowTip(
        "hostservice",
        "hostservice_netfilter",
        "?",
        () => {
          this.$ajax({
            url: "host/" + this.host_id + "/netfilter/handle/rule/delete",
            type: "post",
            data: {
              fid: fids
            }
          })
            .then(function(res) {
              _this.$message({
                type: "success",
                message: "!"
              });
              _this.reload();
            })
            .catch(function() {});
        },
        this
      );
    },
    // 
    rule_on(item) {
      let _this = this;
      let fids = "";
      if (Array.isArray(item)) {
        if (this.$tools.isArrayNull(item)) {
          this.$message({
            type: "error",
            message: ",!"
          });
          return;
        }
        fids = this.$tools.mergeParam(item,'fid')
      } else {
        fids = item.fid
      }
      this.$isShowTip(
        "hostservice",
        "hostservice_netfilter",
        "?",
        () => {
          this.$ajax({
            url: "host/" + this.host_id + "/netfilter/handle/rule/on",
            type: "post",
            data: {
              fid: fids
            }
          })
            .then(function(res) {
              _this.$message({
                type: "success",
                message: "!"
              });
              _this.reload();
            })
            .catch(function() {});
        },
        this
      );
    },
    // 
    rule_off(item) {
      let _this = this;
      let fids = "";
      if (Array.isArray(item)) {
        if (this.$tools.isArrayNull(item)) {
          this.$message({
            type: "error",
            message: ",!"
          });
          return;
        }
        fids = this.$tools.mergeParam(item,'fid')
      } else {
        fids = item.fid
      }
      this.$isShowTip(
        "hostservice",
        "hostservice_netfilter",
        "?",
        () => {
          this.$ajax({
            url: "host/" + this.host_id + "/netfilter/handle/rule/off",
            type: "post",
            data: {
              fid: fids
            }
          })
            .then(function(res) {
              _this.$message({
                type: "success",
                message: "!"
              });
              _this.reload();
            })
            .catch(function() {});
        },
        this
      );
    },
    // 
    reload() {
      this.$refs.hostservice_netfilter_manRule.reload();
    },
    selectChange(select) {
      this.selectData = select;
    },
    // 
    addRule() {
      this.$layer.iframe({
        content: {
          content: addRule,
          parent: this,
          data: {
            item : this.item,
            host_id: this.host_id
          }
        },
        area: ["660px"],
        title: "",
        shadeClose: false
      });
    },
    // 
    lookRule() {
      this.$layer.iframe({
        content: {
          content: lookRule,
          parent: this,
          data: {
            host_id: this.host_id
          }
        },
        area: ["800px", "auto"],
        title: "",
        shadeClose: false
      });
    },
    // 
    manRule_edit(item) {
      this.$layer.iframe({
        content: {
          content: manRule_edit,
          parent: this,
          data: {
            item: item,
            host_id :this.host_id
          }
        },
        area: ["650px"],
        title: "",
        shadeClose: false
      });
    },
    // 
    manRule_del(item) {
      let _this = this;
      this.$isShowTip(
        "hostservice",
        "hostservice_netfilter",
        "?",
        () => {
          this.$ajax({
            url: "host/" + this.host_id + "/netfilter/handle/rule/delete",
            type: "post",
            data: {
              fid: item.fid
            }
          })
            .then(function(res) {
              _this.$message({
                type: "success",
                message: "!"
              });
              _this.reload();
            })
            .catch(function() {});
        },
        this
      );
    }
  }
};
</script>
<style scoped>
</style>



