<template>
  <div class="hostservice-netfilter">
    <div class="tagIcon-notice">
      <span>:</span>
      <span v-for="item in icon.tagIcon" :key="item.id">
        <svg class="icon_1x5" aria-hidden="true" v-html="item.icon" />
        {{ item.name }}
      </span>
    </div>
    <g-el-table
      :tableHeadShow="tableHeadShow"
      :url="url"
      :type="type"
      :cloumns="cloumns"
      :params="params"
      :pageParams="pageParams"
      @selectChange="selectChange"
      ref="hostservice_netfilter_table"
    >
      <template v-slot:table_header>
        <el-button type="primary" size="mini" @click="reload()"></el-button>
        <el-button type="danger" size="mini" @click="removeModule(selectData)"
          ></el-button
        >
        <el-button type="primary" size="mini" @click="addModule()"
          ></el-button
        >
        <el-button type="primary" size="mini" @click="blInit(selectData)"
          ></el-button
        >
        <el-button type="primary" size="mini" @click="bl(selectData)"
          ></el-button
        >
        <el-button type="primary" size="mini" @click="manualRule(selectData)"
          ></el-button
        >

        <el-button type="primary" size="mini" @click="export_rule(selectData)"
          ></el-button
        >
        <el-button type="primary" size="mini" @click="exRule(selectData)"
          ></el-button
        >
        <el-button type="primary" size="mini" @click="setLogOptions(selectData)"
          ></el-button
        >
        <el-button
          type="primary"
          size="mini"
          @click="createExportTask(selectData)"
          ></el-button
        >
        <el-button type="primary" size="mini" @click="exportTaskList"
          ></el-button
        >
        <!-- <el-button type="primary" size="mini" @click="statistical(selectData)"
          ></el-button
        > -->
        
      </template>
      <!--  -->
      <template v-slot:name_head="props">
        <el-input
          v-model="params.name"
          size="mini"
          placeholder=""
          @keyup.enter.native="reload"
          :maxLength="20"
        ></el-input>
      </template>
      <!-- IP -->
      <template v-slot:ip="props">
        <el-popover trigger="hover" placement="top">
          <p>: {{ props.data.row.name }}</p>
          <p>: {{ props.data.row.software.name }}</p>
          <div slot="reference" class="name-wrapper">
            {{ props.data.row.ip }}
          </div>
        </el-popover>
      </template>
      <template v-slot:ip_head="props">
        <el-input
          v-model="params.ip"
          :maxLength="20"
          size="mini"
          placeholder=""
          @keyup.enter.native="reload"
        ></el-input>
      </template>
      <!--  -->
      <template slot="tag" slot-scope="props">
        <span v-for="item of props.data.row.tag" :key="item.id">
          <svg
            class="icon_1x5"
            aria-hidden="true"
            v-html="icon.tagIcon[item.icon].icon"
          />
          <i style="font-size:0;">{{ item.name }}</i>
        </span>
      </template>
      <template v-slot:tag_head="props">
        <el-select
          v-model="params.tag"
          @change="search"
          clearable
          size="mini"
          placeholder=""
        >
          <el-option
            v-for="item in icon.tagIcon"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
      <!--  -->
      <template slot="online" slot-scope="props">
        <div v-if="props.data.row.online == 1">
          <span v-html="icon.linkIcon.link"></span>
          <span v-show="props.data.row.version"
            >[v{{ props.data.row.version }}]</span
          >
        </div>
        <div v-else-if="props.data.row.online == 0">
          <span v-html="icon.linkIcon.unlink"></span><span
            v-show="props.data.row.version"
            >[v{{ props.data.row.version }}]</span
          >
        </div>
      </template>
      <template v-slot:online_sel="props">
        <el-select
          v-model="params.online"
          clearable
          @change="search"
          size="mini"
          placeholder=""
        >
          <el-option
            v-for="item in select.online"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
      <!--  -->
      <template slot="learn_status" slot-scope="props">
        <div v-if="props.data.row.netfilter.learn_status == 1">
          <span v-html="icon.stateIcon.success"></span>
        </div>
        <div v-else-if="props.data.row.netfilter.learn_status == 0">
          <span v-html="icon.stateIcon.error"></span>
        </div>
      </template>
      <template v-slot:learn_status_sel="props">
        <el-select
          v-model="params.learn_status"
          clearable
          @change="reload"
          size="mini"
          placeholder=""
        >
          <el-option
            v-for="item in select.self_learn"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
      <!--  -->
      <template slot="os" slot-scope="props">
        <svg
          class="icon_1x5"
          aria-hidden="true"
          v-html="icon.osIcon[props.data.row.os].icon"
        />
        <span>{{ icon.osIcon[props.data.row.os].name }}</span>
      </template>
      <template v-slot:os_head="props">
        <el-select
          v-model="params.os"
          clearable
          @change="search"
          size="mini"
          placeholder=""
        >
          <el-option
            v-for="item in icon.osIcon"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
      <!-- expand -->
      <template slot="expand" slot-scope="props">
        <el-form label-position="left" inline class="ahsmp-table-expand">
          <!-- <el-form-item label=":">
            <span v-for="item of props.data.row.tag" :key="item.id">
              <svg
                class="icon_1x5"
                aria-hidden="true"
                v-html="icon.tagIcon[item.icon].icon"
              />
              <span>{{ icon.tagIcon[item.icon].name }}</span>
            </span>
          </el-form-item> -->
          <!--  -->
          <el-form-item label=":">
            <el-button
              v-if="props.data.row.netfilter.init == 0"
              type="primary"
              size="mini"
              class="netfilter_btn_width"
              @click="initialization(props.data.row)"
              :disabled="props.data.row.online == 0"
              ></el-button
            >
            <el-button
              v-if="props.data.row.netfilter.init == 1"
              type="primary"
              size="mini"
              class="netfilter_btn_width"
              disabled
              ></el-button
            >
            <el-button
              type="primary"
              size="mini"
              class="netfilter_btn_width"
              @click="autoRuleLearning(props.data.row)"
              :disabled="
                props.data.row.online == 0 || props.data.row.netfilter.init == 0
              "
              ></el-button
            >

            <el-button
              type="primary"
              size="mini"
              class="netfilter_btn_width"
              @click="manRule(props.data.row)"
              :disabled="
                props.data.row.online == 0 || props.data.row.netfilter.init == 0
              "
              ></el-button
            >
            <el-button
              type="primary"
              size="mini"
              class="netfilter_btn_width"
              @click="fictitious_Rule(props.data.row)"
              :disabled="
                props.data.row.online == 0 || props.data.row.netfilter.init == 0
              "
              ></el-button
            >
            <el-button
              type="primary"
              size="mini"
              class="netfilter_btn_width"
              @click="defaultRule(props.data.row)"
              :disabled="
                props.data.row.online == 0 || props.data.row.netfilter.init == 0
              "
              ></el-button
            >
            <el-button
              type="primary"
              size="mini"
              class="netfilter_btn_width"
              @click="originalRule(props.data.row)"
              :disabled="
                props.data.row.online == 0 || props.data.row.netfilter.init == 0
              "
              ></el-button
            >
          </el-form-item>
          
          <!--  -->
          <el-form-item label=":">
            <!--  -->
            <div v-if="props.data.row.netfilter.init == 0">
              <el-button
                type="primary"
                size="mini"
                class="netfilter_btn_width"
                disabled
                ></el-button
              >
              <el-button
                type="primary"
                size="mini"
                @click="LearningConfiguration(props.data.row)"
                disabled
                class="netfilter_btn_width"
                ></el-button
              >
              <el-button
                type="primary"
                size="mini"
                @click="autodateLearning_one(props.data.row)"
                disabled
                class="netfilter_btn_width"
                ></el-button
              >
            </div>
            <!--  -->
            <div v-if="props.data.row.netfilter.init == 1">
              <el-button
                type="primary"
                size="mini"
                @click="study_turn(props.data.row)"
                v-if="props.data.row.netfilter.learn_status == 0"
                class="is_study netfilter_btn_width"
                :disabled="props.data.row.online == 0"
                ></el-button
              >
              <el-button
                type="danger"
                size="mini"
                @click="study_turn(props.data.row)"
                v-if="props.data.row.netfilter.learn_status == 1"
                class="is_study netfilter_btn_width"
                :disabled="props.data.row.online == 0"
                ></el-button
              >
              <el-button
                type="primary"
                size="mini"
                @click="LearningConfiguration(props.data.row)"
                :disabled="props.data.row.online == 0"
                class="netfilter_btn_width"
                ></el-button
              >
              <el-button
                type="primary"
                size="mini"
                class="netfilter_btn_width"
                @click="autodateLearning_one(props.data.row)"
                :disabled="props.data.row.online == 0"
                ></el-button
              >
            </div>
          </el-form-item>
          <!--  -->
          <el-form-item label=":">
            <el-button
              type="primary"
              size="mini"
              class="netfilter_btn_width"
              @click="deploy(props.data.row)"
              v-if="props.data.row.netfilter.init == 0"
              disabled
              ></el-button
            >
            <el-button
              type="primary"
              size="mini"
              class="netfilter_btn_width"
              @click="deploy(props.data.row)"
              v-if="props.data.row.netfilter.init == 1"
              :disabled="props.data.row.online == 0"
              ></el-button
            >
          </el-form-item>
          <!--  -->
          <el-form-item label=":">
            <el-button
              type="primary"
              size="mini"
              class="netfilter_btn_width"
              @click="openLog(props.data.row)"
              v-if="props.data.row.netfilter.log_status == 0"
              :disabled="props.data.row.online == 0"
              ></el-button
            >
            <el-button
              type="danger"
              size="mini"
              class="netfilter_btn_width"
              @click="closeLog(props.data.row)"
              :disabled="props.data.row.online == 0"
              v-else
              ></el-button
            >
            <el-button
              type="primary"
              size="mini"
              class="netfilter_btn_width"
              @click="showLog(props.data.row)"
              ></el-button
            >
            <el-button
              type="primary"
              size="mini"
              class="netfilter_btn_width"
              @click="showIngoreLog(props.data.row)"
              ></el-button
            >
            <el-button
              type="primary"
              size="mini"
              class="netfilter_btn_width"
              @click="setLogOptions(props.data.row)"
              ></el-button
            >
            <el-button
              type="primary"
              size="mini"
              class="netfilter_btn_width"
              @click="createExportTask(props.data.row)"
              ></el-button
            >
          </el-form-item>
          <!--  -->
          <el-form-item label=":">
            <el-button
              class="netfilter_btn_width"
              type="primary"
              size="mini"
              @click="hitWaring(props.data.row)"
              :disabled="
                props.data.row.online == 0 || props.data.row.netfilter.init == 0
              "
              ></el-button
            >
            <el-button
              class="netfilter_btn_width"
              type="primary"
              size="mini"
              @click="realWarning(props.data.row)"
              :disabled="
                props.data.row.online == 0 || props.data.row.netfilter.init == 0
              "
              ></el-button
            >
          </el-form-item>
          
        </el-form>
      </template>
    </g-el-table>
  </div>
</template>

<script>
import bl from "views/hostservice/netfilter/batchLearning/"; //
import exRule from "views/hostservice/netfilter/exportRule/"; //
import manualRule from "views/hostservice/netfilter/manualRule/"; //
import LearningConfiguration from "views/hostservice/netfilter/LearningConfiguration/"; //
import autodateLearning_one from "views/hostservice/netfilter/autoDateLearning_one/"; //
import autoRuleLearning from "views/hostservice/netfilter/autoRuleLearning/"; //
import defaultRule from "views/hostservice/netfilter/defaultRule/"; //
import originalRule from "views/hostservice/netfilter/originalRule/"; //
import deploy from "views/hostservice/netfilter/deploy/"; //
import manRule from "views/hostservice/netfilter/manRule/"; //
import fictitious_Rule from "views/hostservice/netfilter/fictitious_Rule/"; //
import lineBrowse from "views/home/line_browse";
import { logOpen, logClose } from "req/api/log/netfilterlog";
import moduleRefresh from "common/mixins/modulRefresh"
import setLogOptions from "./netfilterlog/set_logging_options";
import createExportTask from "./netfilterlog/create_export_task";
import exportLogTaskList from "./netfilterlog/export_log_task_list";
import showLogList from "./netfilterlog/show_log_list";
import addModule from "views/hostservice/addModule";
import { delModule } from "req/api/hostservice/addModule";
import hitAlarm from "views/hostservice/netfilter/hit_warning";
import realAlarm from "views/hostservice/netfilter/real_warning";

export default {
  data() {
    return {
      url: "/host/list/netfilter",
      type: "post",
      cloumns: [
        {
          type: "selection",
          fixed: "left"
        },
        {
          type: "index",
          // fixed: "left",
          label: "",
          second_head: {
            type: "index",
            width: "60px",
            label: " ",
            indexMethod: function(index) {
              return index + 1;
            }
          }
        },
        {
          label: "",
          sortable: true,
          prop: "name",
          second_head: {
            prop: "name",
            templateHeadName: "name_head",
            width: "200px"
          }
        },
        {
          label: "IP",
          sortable: true,
          sortMethod: this.$tools.IPSort,
          prop: "ip",
          second_head: {
            isTemplate: true,
            templateName: "ip",
            prop: "ip",
            templateHeadName: "ip_head",
            width: "200px"
          }
        },
        {
          label: "",
          prop: "os",
          sortable: true,
          second_head: {
            width: "180px",
            isTemplate: true,
            templateName: "os",
            templateHeadName: "os_head",
            prop: "os"
          }
        },
        {
          label: "",
          prop: "tag",
          sortable: true,
          second_head: {
            isTemplate: true,
            templateName: "tag",
            templateHeadName: "tag_head",
            prop: "tag",
            minWidth: "150px"
          }
        },
        {
          label: "",
          prop: "online",
          sortable: true,
          second_head: {
            isTemplate: true,
            templateName: "online",
            templateHeadName: "online_sel",
            width: "150px",
            prop: "online"
          }
        },
        {
          label: "",
          prop: "learn_status",
          sortable: true,
          second_head: {
            isTemplate: true,
            templateName: "learn_status",
            templateHeadName: "learn_status_sel",
            width: "150px",
            prop: "learn_status"
          }
        },
        // {
        //   label: "",
        //   isTemplate: true,
        //   templateName: "operation",
        //   second_head: {
        //     isTemplate: true,
        //     templateName: "operation",
        //     width: "330px"
        //   }
        // },
        // {
        //   label: "",
        //   isTemplate: true,
        //   templateName: "operation1",
        //   second_head: {
        //     isTemplate: true,
        //     templateName: "operation1",
        //     width: "190px"
        //   }
        // },
        // {
        //   label: "",
        //   second_head: {
        //     isTemplate: true,
        //     templateName: "netfilterlog",
        //     width: "180px",
        //     templateHeadName: "netfilterlog_head"
        //   }
        // },
        // {
        //   label: "",
        //   isTemplate: true,
        //   templateName: "operation3",
        //   width: "80px",
        //   second_head: {
        //     isTemplate: true,
        //     templateName: "operation3",
        //     width: "80px"
        //   }
        // },
        {
          type: "expand",
          fixed: "right",
          width: "60px",
          isTemplate: true,
          templateName: "expand"
        }
      ],
      tableHeadShow: true,
      params: {
        page: 1,
        limit: 10,
        name: "",
        ip: "",
        online: "",
        learn_status: "",
        os: ""
      },
      pageParams: {
        layout: "total, sizes, prev, pager, next, jumper",
        pageAlign: "left",
        pageSizes: [10, 20, 30]
      },
      icon: {
        osIcon: this.$icons.osIcon,
        stateIcon: this.$icons.stateIcon,
        linkIcon: this.$icons.linkIcon,
        tagIcon: this.$icons.tagIcon
      },
      select: {
        // 
        online: [
          {
            id: 1,
            name: ""
          },
          {
            id: 0,
            name: ""
          }
        ],
        // 
        self_learn: [
          {
            id: 1,
            name: ""
          },
          {
            id: 0,
            name: ""
          }
        ]
      },
      selectData: []
    };
  },
  mixins: [moduleRefresh("hostservice_netfilter_table")],
  methods: {
    blInit(item){
      var _this = this;
      let ids = "";
      if (Array.isArray(item)) {
        if (this.$tools.isArrayNull(item)) {
          this.$message({
            type: "error",
            message: "!!"
          });
          return;
        }
        ids = this.$tools.mergeId(item);
      } else {
        ids = item.id;
      }
      this.$ajax({
        url: "host/netfilter/moreinitialization",
        type: "post",
        data: {
          host_id:ids
        }
      })
        .then(function(res) {
          _this.reload();
          _this.$message({
            type: "success",
            message: "!"
          });
        })
        .catch(function() {});
    },
    addModule() {
      this.$layer.iframe({
        content: {
          content: addModule,
          parent: this,
          data: {
            moduleType: "netfilter",
            tipText: "hostservice_netfilter"
          }
        },
        area: ["700px", "auto"],
        title: " (  )",
        shadeClose: false
      });
    },
    removeModule(item) {
      let ids = "";
      if (Array.isArray(item)) {
        if (this.$tools.isArrayNull(item)) {
          this.$message({
            type: "error",
            message: "!"
          });
          return;
        }
        ids = this.$tools.mergeId(item);
      } else {
        ids = item.id;
      }
      this.$isShowTip(
        "hostservice",
        "hostservice_netfilter",
        "?",
        () => {
          this.__delModule({ host_id: ids, module: "netfilter" });
        },
        this
      );
    },
    __delModule(param) {
      delModule(param)
        .then(res => {
          this.$message({
            type: "success",
            message: ""
          });
          this.reload();
        })
        .catch(err => {});
    },
    statistical(item) {
      this.$layer.iframe({
        content: {
          content: lineBrowse,
          parent: this,
          data: {
            select_data: ""
          }
        },
        area: ["900px", "auto"],
        title: "",
        shadeClose: false
      });
    },
    createExportTask(item) {
      let ids = "";
      if (Array.isArray(item)) {
        if (this.$tools.isArrayNull(item)) {
          this.$message({
            type: "error",
            message: "!"
          });
          return;
        }
        ids = this.$tools.mergeId(item);
      } else {
        ids = item.id.toString();
      }
      this.$layer.iframe({
        content: {
          content: createExportTask,
          parent: this,
          data: {
            host_id: ids
          }
        },
        area: ["500px", "auto"],
        title: "",
        shadeClose: false
      });
    },
    exportTaskList() {
      this.$layer.iframe({
        content: {
          content: exportLogTaskList,
          parent: this,
          data: {}
        },
        area: ["800px", "auto"],
        title: "",
        shadeClose: false
      });
    },
    setLogOptions(item) {
      let ids = "";
      let isBatch = false;
      if (Array.isArray(item)) {
        if (this.$tools.isArrayNull(item)) {
          this.$message({
            type: "error",
            message: "!"
          });
          return;
        }
        isBatch = true;
        ids = this.$tools.mergeId(item);
      } else {
        isBatch = false;
        ids = item.id.toString();
      }
      this.$layer.iframe({
        content: {
          content: setLogOptions,
          parent: this,
          data: {
            host_id: ids,
            isBatch: isBatch
          }
        },
        area: ["500px", "auto"],
        title: "",
        shadeClose: false
      });
    },
    hitWaring(item) {
      this.$layer.iframe({
        content: {
          content: hitAlarm,
          parent: this,
          data: {
            layer_data: item
          }
        },
        area: ["1000px", "auto"],
        title: " (  )",
        shadeClose: false
      });
    },
    realWarning(item) {
      this.$layer.iframe({
        content: {
          content: realAlarm,
          parent: this,
          data: {
            layer_data:item
          }
        },
        area: ["1200px", "auto"],
        title: "",
        shadeClose: false
      });
    },
    showLog(item) {
      this.$layer.iframe({
        content: {
          content: showLogList,
          parent: this,
          data: {
            layer_data: item,
            isIgnore: 0
          }
        },
        area: ["850px", "auto"],
        title: "",
        shadeClose: false
      });
    },
    showIngoreLog(item) {
      this.$layer.iframe({
        content: {
          content: showLogList,
          parent: this,
          data: {
            layer_data: item,
            isIgnore: 1
          }
        },
        area: ["850px", "auto"],
        title: "",
        shadeClose: false
      });
    },
    openLog(item) {
      this.$isShowTip(
        "hostservice",
        "hostservice_netfilter",
        "",
        () => {
          this.__logOpen({
            id: item.id
          });
        },
        this
      );
    },
    closeLog(item) {
      this.$isShowTip(
        "hostservice",
        "hostservice_netfilter",
        "",
        () => {
          this.__logClose({
            id: item.id
          });
        },
        this
      );
    },
    __logOpen(param) {
      return logOpen(param)
        .then(res => {
          this.$message({
            type: "success",
            message: ""
          });
          this.reload();
        })
        .catch(err => {});
    },
    __logClose(param) {
      return logClose(param)
        .then(res => {
          this.$message({
            type: "success",
            message: ""
          });
          this.reload();
        })
        .catch(err => {});
    },
    // 
    study_turn(item) {
      let _this = this;
      var study_inner_val = "";
      if (item.netfilter.learn_status == 0) {
        study_inner_val = "";
      } else {
        study_inner_val = "";
      }
      // 
      if (study_inner_val == "") {
        this.$ajax({
          url: "host/netfilter/learn/on",
          type: "post",
          data: {
            host_id: item.id
          }
        })
          .then(function(res) {
            _this.reload();
            _this.$message({
              type: "success",
              message: "!"
            });
          })
          .catch(function() {});
      } else if (study_inner_val == "") {
        this.$ajax({
          url: "host/" + item.id + "/netfilter/learn/off",
          type: "post",
          data: {}
        })
          .then(function(res) {
            _this.$message({
              type: "success",
              message: "!"
            });
            _this.reload();
          })
          .catch(function() {});
      }
    },
    // 
    selectChange(select) {
      this.selectData = select;
    },
    // 
    initialization(item) {
      var _this = this;
      this.$ajax({
        url: "host/" + item.id + "/netfilter/initialization",
        type: "post",
        data: {
          os: item.os
        }
      })
        .then(function(res) {
          _this.reload();
          _this.$message({
            type: "success",
            message: "!"
          });
        })
        .catch(function() {});
    },
    search() {
      this.params.page = 1;
      this.reload();
    },
    // 
    bl(item) {
      let ids = "";
      if (Array.isArray(item)) {
        if (this.$tools.isArrayNull(item)) {
          this.$message({
            type: "error",
            message: "!!"
          });
          return;
        }
        ids = this.$tools.mergeId(item);
      } else {
        ids = item.id;
      }
      this.$layer.iframe({
        content: {
          content: bl,
          parent: this,
          data: {
            host_id: ids
          }
        },
        area: ["550px", "auto"],
        title: "",
        shadeClose: false
      });
    },
    exRule() {
      //
      this.$layer.iframe({
        content: {
          content: exRule,
          parent: this,
          data: {
            select_data: ""
          }
        },
        area: ["900px", "auto"],
        title: "",
        shadeClose: false
      });
    },
    manualRule(item) {
      //  
      let ids = "";
      if (Array.isArray(item)) {
        if (this.$tools.isArrayNull(item)) {
          this.$message({
            type: "error",
            message: "!!"
          });
          return;
        }
        ids = this.$tools.mergeId(item);
      } else {
        ids = item.id;
      }
      var ips = [];
      for (var tag of item) {
        ips.push(tag.ip);
      }
      var ipstr = ips.join(",");
      this.$layer.iframe({
        content: {
          content: manualRule,
          parent: this,
          data: {
            host_id: ids,
            ipstr: ipstr
          }
        },
        area: ["620px", "650px"],
        title: "",
        shadeClose: false
      });
    },
    // 
    export_rule(item) {
      let _this = this;
      let ids = "";
      if (Array.isArray(item)) {
        if (this.$tools.isArrayNull(item)) {
          this.$message({
            type: "error",
            message: "!!"
          });
          return;
        }
        ids = this.$tools.mergeId(item);
      } else {
        ids = item.id;
      }
      this.$isShowTip(
        "hostservice",
        "hostservice_netfilter",
        "?",
        () => {
          //  
          this.$ajax({
            url: "download/create/2",
            type: "post",
            data: {
              host_id: ids
            }
          })
            .then(function(res) {
              _this.$message({
                type: "success",
                message: "!"
              });
              _this.reload();
            })
            .catch(function() {});
        },
        this
      );
    },
    LearningConfiguration(item) {
      //  
      this.$layer.iframe({
        content: {
          content: LearningConfiguration,
          parent: this,
          data: {
            host_id: item.id
          }
        },
        area: ["550px", "auto"],
        title: "",
        shadeClose: false
      });
    },
    autodateLearning_one(item) {
      //  
      this.$layer.iframe({
        content: {
          content: autodateLearning_one,
          parent: this,
          data: {
            host_id: item.id
          }
        },
        area: ["850px", "auto"],
        title: "",
        shadeClose: false
      });
    },
    autoRuleLearning(item) {
      //  
      this.$layer.iframe({
        content: {
          content: autoRuleLearning,
          parent: this,
          data: {
            host_id: item.id
          }
        },
        area: ["1750px", "auto"],
        title: "",
        shadeClose: false
      });
    },
    defaultRule(item) {
      //  
      this.$layer.iframe({
        content: {
          content: defaultRule,
          parent: this,
          data: {
            host_id: item.id
          }
        },
        area: ["680px", "auto"],
        title: "",
        shadeClose: false
      });
    },
    // 
    originalRule(item) {
      this.$layer.iframe({
        content: {
          content: originalRule,
          parent: this,
          data: {
            host_id: item.id
          }
        },
        area: ["680px", "auto"],
        title: "",
        shadeClose: false
      });
    },
    deploy(item) {
      //  
      this.$layer.iframe({
        content: {
          content: deploy,
          parent: this,
          data: {
            host_id: item.id,
            firewall: item.netfilter.firewall
          }
        },
        area: ["622px", "auto"],
        title: "",
        shadeClose: false
      });
    },
    manRule(item) {
      //  
      this.$layer.iframe({
        content: {
          content: manRule,
          parent: this,
          data: {
            item: item,
            host_id: item.id
          }
        },
        area: ["1660px", "auto"],
        title: " ",
        shadeClose: false
      });
    },
    fictitious_Rule(item) {
      //  
      this.$layer.iframe({
        content: {
          content: fictitious_Rule,
          parent: this,
          data: {
            item: item,
            host_id: item.id
          }
        },
        area: ["1840px", "auto"],
        title: "  (  )",
        shadeClose: false
      });
    }
  }
};
</script>

<style scoped>
.netfilter_btn_width {
  width: 120px;
}
.hostservice-netfilter .tagIcon-notice {
  /* height: 50px; */
  line-height: 50px;
}
.hostservice-netfilter .tagIcon-notice span {
  margin-right: 10px;
}
.hostservice-netfilter .tagIcon-notice svg {
  vertical-align: middle;
}
.hostservice-netfilter .ahsmp-table-expand .el-form-item {
  width: 100%;
  margin-right: 0;
  margin-bottom: 0;
  line-height: 1;
}
</style>



