<template>
  <div
    class="hostservice_netfilter_batchLearning"
    style="width: 100%;padding: 8px;"
  >
    <mu-form
      :model="formData"
      class="mu-demo-form"
      :label-position="labelPosition"
      label-width="90"
      ref="hostservice_netfilter_batchLearning"
    >
      <mu-form-item
        prop="time"
        label=" "
        label-width="130"
        :rules="select_time"
      >
        <mu-select v-model="formData.time" style="width:70%">
          <mu-option
            v-for="option in timeData"
            :key="option.id"
            :label="option.name"
            :value="option.id"
          ></mu-option>
        </mu-select>
      </mu-form-item>

      <div v-if="formData.time == '0'">
        <mu-form-item prop="radio" label="" label-width="130">
          <p>{{ date | formaDate }}</p>
        </mu-form-item>

        <mu-form-item
          prop="autotime"
          label=""
          label-width="130"
          style="width:70%"
          :rules="select_autotime"
        >
          <!-- <mu-date-input v-model="formData.autotime" type="dateTime" label-float full-width landscape></mu-date-input> -->

          <mu-text-field
            v-model="formData.autotime"
            :max-length="50"
            @click="openDate('custom_time_picker')"
            ref="custom_date_self"
          ></mu-text-field>
          <div class="form_date_custom">
            <el-date-picker
              v-model="formData.autotime"
              ref="custom_time_picker"
              format="yyyy-MM-dd HH:mm:ss"
              value-format="yyyy-MM-dd HH:mm:ss"
              type="datetime"
              placeholder=""
              @change="openDate('custom_date_self')"
            >
            </el-date-picker>
          </div>
        </mu-form-item>
      </div>

      <div class="xian"></div>
      <mu-form-item prop="auto" label="" label-width="130">
        <mu-radio
          v-model="formData.auto"
          value="1"
          label=""
        ></mu-radio>
        <mu-radio
          v-model="formData.auto"
          value="2"
          label="()"
        ></mu-radio>
      </mu-form-item>

      <mu-form-item prop="model" label="" label-width="130">
        <mu-radio
          v-model="formData.model"
          value="1"
          label=""
        ></mu-radio>
        <mu-radio
          v-model="formData.model"
          value="2"
          label=""
          class="cumulative_study"
        ></mu-radio>
      </mu-form-item>

      <mu-form-item prop="allow" label="" label-width="130">
        <mu-radio v-model="formData.allow" value="1" label=""></mu-radio>
        <mu-radio
          v-model="formData.allow"
          value="0"
          label=""
          class="forbid"
        ></mu-radio>
      </mu-form-item>

      <div class="xian"></div>
      <mu-form-item prop="checkbox" label-width="130">
        <mu-checkbox
          v-model="formData.have_mac_addr"
          label=""
        ></mu-checkbox>
        <mu-checkbox
          v-model="formData.now"
          label=""
          class="study_now"
        ></mu-checkbox>
      </mu-form-item>
      <mu-form-item style="margin-left:25%">
        <mu-button color="primary" @click="sure"></mu-button>
      </mu-form-item>
    </mu-form>
  </div>
</template>
<script>
import openDatePicker from "common/js/datebug";
var padaDate = function(value) {
  return value < 10 ? "0" + value : value;
};
export default {
  name: "hostservice_netfilter_batchLearning",
  data() {
    return {
      date: new Date(), //
      labelPosition: "right",
      select_time: [
        { validate: val => !!val, message: "" }
      ],
      select_autotime: [
        { validate: val => !!val, message: "" }
      ],
      formData: {
        host_id: this.host_id,
        time: "",
        autotime: "",
        auto: "1",
        model: "1",
        allow: "1",
        now: true,
        have_mac_addr: false
      },
      timeData: [
        {
          id: "900",
          name: "15"
        },
        {
          id: "1800",
          name: "30"
        },
        {
          id: "3600",
          name: "1"
        },
        {
          id: "21600",
          name: "6"
        },
        {
          id: "86400",
          name: "1"
        },
        {
          id: "604800",
          name: "7"
        },
        {
          id: "1296000",
          name: "15"
        },
        {
          id: "2592000",
          name: "1"
        },
        {
          id: "0",
          name: ""
        }
      ]
    };
  },
  props: {
    host_id: String,
    layerid: {
      type: String
    }
  },
  methods: {
    openDate(param) {
      let _this = this;
      openDatePicker(param, _this);
    },
    // 
    sure() {
      this.$refs.hostservice_netfilter_batchLearning.validate().then(result => {
        if (result) {
          let _this = this;
          this.$isShowTip(
            "hostservice",
            "hostservice_netfilter",
            "?",
            () => {
              if (this.formData.time == 0) {
                // 
                let curTime = new Date().getTime();
                // 
                let endTime = new Date(this.formData.autotime).getTime();
                let keep_time = endTime - curTime;
                if (keep_time < 0) {
                  this.$message({
                    type: "error",
                    message: "!"
                  });
                  return;
                }
                this.formData.keep_time = parseInt(keep_time / 1000);
              } else {
                this.formData.keep_time = this.formData.time;
              }
              let data = JSON.parse(JSON.stringify(this.formData));
              if (data.have_mac_addr) {
                data.have_mac_addr = 1;
              } else {
                data.have_mac_addr = 0;
              }
              if (data.now) {
                data.now = 1;
              } else {
                data.now = 0;
              }
              this.$ajax({
                url: "host/netfilter/learn/on",
                type: "post",
                data: data
              })
                .then(function(res) {
                  _this.$message({
                    type: "success",
                    message: "!"
                  });
                  _this.$layer.close(_this.layerid);
                  _this.$parent.$refs.hostservice_netfilter_table.reload();
                })
                .catch(function() {});
            },
            this
          );
        }
      });
    }
  },
  filters: {
    //
    formaDate: function(value) {
      //
      var date = new Date();
      var year = date.getFullYear(); //
      var month = padaDate(date.getMonth() + 1); //
      var day = padaDate(date.getDate()); //
      var hours = padaDate(date.getHours()); //
      var minutes = padaDate(date.getMinutes()); //
      var seconds = padaDate(date.getSeconds()); //
      //
      return (
        year +
        "-" +
        month +
        "-" +
        day +
        " " +
        hours +
        ":" +
        minutes +
        ":" +
        seconds
      );
    }
  },
  mounted: function() {
    //
    var _this = this;
    this.timeId = setInterval(function() {
      _this.date = new Date();
    }, 1000);
  },
  beforeDestroy: function() {
    //
    if (this.timeId) {
      clearInterval(this.timeId);
    }
  }
};
</script>
<style scoped>
.xian {
  height: 1px;
  background-color: #ccc;
  margin: 5px 10px;
}
.mu-form-item {
  margin-bottom: 1px;
  padding-bottom: 1px;
}
.cumulative_study {
  margin-left: 15px;
}
.forbid {
  margin-left: 71px;
}
.study_now {
  margin-left: 15px;
}
</style>



